@import url("https://fonts.googleapis.com/css2?family=Manrope:wght@600&display=swap");
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

@font-face {
  font-family: "Gotham Pro bold";
  src: url("../fonts/gothampro_bold.ttf");
  font-weight: bold;
  font-style: bold;
}
@font-face {
  font-family: "GothamPro regular";
  src: url("../fonts/gothampro_black.ttf");
  font-weight: normal;
  font-style: normal;
}
body {
  overflow-x: hidden;
  background-color: #ECF3FB;
}

.header {
  width: 100%;
  background-color: white;
}
.header__container {
  margin: 0 120px;
}
.header__wrapper {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.header-logo__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header-link__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__subscription {
  position: relative;
}
.header__subscription_active {
  color: #333;
}
.header__subscription_active:hover {
  color: var(--blue, #1749B3);
}
.header-subscriptions__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 27px 24px 24px 23px;
  background-color: white;
  border-radius: 16px;
  position: absolute;
  top: 39px;
  left: -30px;
  width: 176px;
  opacity: 1;
  z-index: 50;
  transition: all ease 0.2s;
}
.header-subscriptions__wrapper_active {
  z-index: -10;
  opacity: 0;
}
.header-subscriptions__link {
  margin-top: 20px;
  font-family: "Gotham Pro regular", sans-serif;
  transition: all ease 0.2s;
  cursor: pointer;
}
.header-subscriptions__link a {
  font-family: "Gotham Pro bold", sans-serif;
  text-decoration: none;
  color: var(--black-2, #171717);
}
.header-subscriptions__link a:hover {
  color: var(--blue, #1749B3);
}
.header-subscriptions__link:first-child {
  margin-top: 0px;
}
.header__name {
  font-family: "Manrope", sans-serif;
  margin-left: 8px;
  position: relative;
}
.header__name a {
  color: #171717;
  text-decoration: none;
}
.header__name_active {
  color: #333;
}
.header__name_active:hover {
  color: var(--blue, #1749B3);
}
.header__arrow {
  margin-left: 4px;
  transform: rotate(-180deg);
  transition: all ease 0.2s;
}
.header__arrow_active {
  transform: rotate(0);
}
.header__menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__link {
  user-select: none;
  font-family: "Gotham Pro regular", sans-serif;
  color: #333;
  cursor: pointer;
}
.header__link:nth-child(2) {
  margin-left: 44px;
}
.header__link:nth-child(3) {
  margin-left: 46px;
}
.header__link:nth-child(4) {
  margin-left: 48px;
}
.header__link:not(:first-child):hover {
  color: var(--blue, #1749B3);
}
.header__activity {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header-maessenger {
  cursor: pointer;
}
.header-maessenger:last-child {
  margin-left: 12px;
}
.header__language {
  margin-left: 40px;
  font-family: "Gotham Pro bold", sans-serif;
  position: relative;
  user-select: none;
  cursor: pointer;
}
.header-language-name {
  position: relative;
}
.header-language-name_active {
  color: #333;
}
.header-language-name_active:hover {
  color: var(--blue, #1749B3);
}
.header-language__option {
  cursor: pointer;
  margin-top: 20px;
  color: var(--black-2, #171717);
}
.header-language__option:hover {
  color: var(--blue, #1749B3);
}
.header-language__option:first-child {
  margin-top: 0;
}
.header-language__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 27px 27px 24px 23px;
  background-color: white;
  border-radius: 16px;
  position: absolute;
  top: 39px;
  opacity: 1;
  z-index: 50;
  transition: all ease 0.2s;
}
.header-language__wrapper_active {
  z-index: -10;
  opacity: 0;
}
.header__log-in {
  color: white;
  font-size: 17px;
  font-family: "Gotham Pro bold", sans-serif;
  font-weight: 700;
  margin-left: 40px;
  padding: 9px 41px;
  border-radius: 8px;
  background-color: #1749B3;
}